/* sanitize.css v3.0.0 | CC0 1.0 Public Domain | github.com/10up/sanitize.css */
audio:not([controls]) {
  display: none;
}
button {
  overflow: visible;
  -webkit-appearance: button;
}
details {
  display: block;
}
html {
  -ms-overflow-style: -ms-autohiding-scrollbar;
  overflow-y: scroll;
  -webkit-text-size-adjust: 100%;
}
input {
  -webkit-border-radius: 0;
}
input[type="button"],
input[type="reset"],
input[type="submit"] {
  -webkit-appearance: button;
}
input[type="number"] {
  width: auto;
}
input[type="search"] {
  -webkit-appearance: textfield;
}
input[type="search"]::-webkit-search-cancel-button,
input[type="search"]::-webkit-search-decoration {
  -webkit-appearance: none;
}
main {
  display: block;
}
pre {
  overflow: auto;
}
progress {
  display: inline-block;
}
small {
  font-size: 75%;
}
summary {
  display: block;
}
svg:not(:root) {
  overflow: hidden;
}
template {
  display: none;
}
textarea {
  overflow: auto;
}
[hidden] {
  display: none;
}
*,
::before,
::after {
  box-sizing: inherit;
}
* {
  font-size: inherit;
  line-height: inherit;
}
::before,
::after {
  text-decoration: inherit;
  vertical-align: inherit;
}
*,
::before,
::after {
  border-style: solid;
  border-width: 0;
}
* {
  background-repeat: no-repeat;
  margin: 0;
  padding: 0;
}
:root {
  background-color: #fff;
  box-sizing: border-box;
  color: #000;
  cursor: default;
  font: 100%/1.5 sans-serif;
  text-rendering: optimizeLegibility;
}
a {
  text-decoration: none;
}
audio,
canvas,
iframe,
img,
svg,
video {
  vertical-align: middle;
}
button,
input,
select,
textarea {
  background-color: transparent;
}
button,
input,
select,
textarea {
  color: inherit;
  font-family: inherit;
  font-style: inherit;
  font-weight: inherit;
}
button,
[type="button"],
[type="date"],
[type="datetime"],
[type="datetime-local"],
[type="email"],
[type="month"],
[type="number"],
[type="password"],
[type="reset"],
[type="search"],
[type="submit"],
[type="tel"],
[type="text"],
[type="time"],
[type="url"],
[type="week"],
select,
textarea {
  min-height: 1.5;
}
code,
kbd,
pre,
samp {
  font-family: monospace, monospace;
}
nav ol,
nav ul {
  list-style: none;
}
select {
  -moz-appearance: none;
  -webkit-appearance: none;
}
select::-ms-expand {
  display: none;
}
select::-ms-value {
  color: currentColor;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}
textarea {
  resize: vertical;
}
::-moz-selection {
  background-color: #b3d4fc;
  color: #fff;
  text-shadow: none;
}
::selection {
  background-color: #b3d4fc;
  color: #fff;
  text-shadow: none;
}
[aria-busy="true"] {
  cursor: progress;
}
[aria-controls] {
  cursor: pointer;
}
[aria-disabled] {
  cursor: default;
}
[hidden][aria-hidden="false"] {
  clip: rect(0 0 0 0);
  display: inherit;
  position: absolute;
}
[hidden][aria-hidden="false"]:focus {
  clip: auto;
}
* {
  width: 100%;
  margin: 0;
}
html,
body,
#app {
  min-height: 100%;
}
.root {
  position: relative;
  min-width: 980px;
  min-height: 100%;
  padding-left: 240px;
}
.root__sideMenu {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  width: 240px;
}
.sideMenu {
  background: #1b64c6;
}
.sideMenu__list__item {
  display: block;
  padding: 0.5rem 0;
}
.sideMenu__list__item:hover {
  background: rgba(255,255,255,0.122);
}
